body {
  margin: 0;
  line-height: 1.5;
  /*padding: 4em 1em;*/
  color: #555;
  font-family: "Helvetica", "Arial", sans-serif;
  margin-top: 1em;
  padding-top: 1em;
  font-size: 1em;
}

h1, h2, strong {
  color: #333;
}

button {
  margin: .5em;
  padding: .5em;
  font-size: 1.25em;
}

#board {
  border-collapse: collapse;
}

li {
  text-align: left;
  max-width: 25em;
}

td {
  text-align: center;
  font-weight: bold;
  font-size: 1.5em;
  color: #555;
  width: 3em;
  height: 3em;
  line-height: 3em;
  border: 3px solid #aaa;
}

td.active {
  cursor: pointer;
}

